import './App.css';
import {HashRouter as Router, Route} from 'react-router-dom' 
import Home from './pages/home';
import Portfolio from './pages/portfolio';





function App() {
  return (
    <Router basename='/'>
<Route exact path='/' component={Home} />
<Route exact path='/portfolio' component={Portfolio} />
    </Router>
  );
}

export default App;
